import React, { useContext, useEffect, useRef, useState } from "react";

import Player from "./Player";
import ChannelList from "./ChannelList";
import { TVChannelsContext } from "./TVChannels"; // Import the context
import ChannelOverlay from "./ProgramOverlay";
import FullscreenToggle from './FullscreenToggle';


const MainUI: React.FC = () => {
  const [isInactive, setIsInactive] = useState(false);
  const [isChannelListOpen, setIsChannelListOpen] = useState<Boolean>(false);
  const inactivityTimer = useRef<ReturnType<typeof setInterval> | null>(null);
  const context = useContext(TVChannelsContext);
  if (!context) {
    throw new Error("MainUI must be used within a TVChannels provider");
  }
  const { channels, currentChannel, switchChannelById } = context;

  const handleActivity = () => {
    setIsInactive(false);
    if (inactivityTimer.current) clearTimeout(inactivityTimer.current);
    inactivityTimer.current = setTimeout(() => {
      setIsInactive(true);
    }, process.env.NODE_ENV !== 'production' ?5000 :5000); // 5 seconds
  };
  useEffect(() => {
    document.addEventListener('mousemove', handleActivity);
    document.addEventListener('mousedown', handleActivity);
    document.addEventListener('keydown', handleActivity);
    document.addEventListener('touchstart', handleActivity);

    return () => {
      document.removeEventListener('mousemove', handleActivity);
      document.removeEventListener('mousedown', handleActivity);
      document.removeEventListener('keydown', handleActivity);
      document.removeEventListener('touchstart', handleActivity);
    };
  }, []);

  useEffect(() => {
    if (isInactive) {
      document.body.style.cursor = 'none';
    } else {
      document.body.style.cursor = 'default';
    }
  }, [isInactive]);

  return (
    <div style={{ position: "relative", width: "100vw", height: "100vh", paddingRight: isChannelListOpen ? "16rem" :"0" }}>
      <Player url={currentChannel?.streamUrl || ""} />
      {!isInactive && (<ChannelOverlay topMenu={isChannelListOpen}/> )}        
      <ChannelList
        channels={channels.getAllChannels()}
        onChannelClick={(channel) => {
          switchChannelById(channel.id);
        }}
        onShow={(channelsOpen) =>{
          setIsChannelListOpen(channelsOpen);
        }}
        userInactive={isInactive}
      />
      {!isInactive && (<FullscreenToggle topMenu={isChannelListOpen}/>)}
    </div>
  );
};

export default MainUI;
